import Vue from "vue";
import Router from "vue-router";
import courseRouter from "./modules/course";
import courseHistoryRouter from "./modules/history";
import courseSpaceRouter from "./modules/space";
import { LimitType } from "@/utils/enum";

import IndexPage from "@/pages/home/index";

Vue.use(Router);

const routes = [
  {
    path: "/",
    redirect: "/login"
  },
  {
    path: "/login",
    meta: { title: "", from: "", limits: [LimitType.top] },
    component: () => import("@/pages/account/login.vue")
  },
  {
    path: "/agreement",
    meta: { title: "", from: "" },
    component: () => import("@/pages/user/agreement.vue")
  },
  {
    path: "/policy",
    meta: { title: "", from: "" },
    component: () => import("@/pages/user/policy.vue")
  },
  {
    path: "/index",
    name: "index",
    component: IndexPage,
    redirect: "/home-page",
    children: [
      {
        path: "/home-page",
        name: "homePage",
        meta: { title: "", from: "", cache: true, limits: [LimitType.top] },
        component: () => import("@/pages/home/home-page.vue")
      },
      {
        path: "/home-course",
        name: "homeCourse",
        meta: { title: "", from: "", cache: true, limits: [LimitType.top] },
        component: () => import("@/pages/home/home-course.vue")
      },
      {
        path: "/my-zone",
        name: "myZone",
        meta: {
          title: "",
          from: "",
          cache: true,
          limits: [LimitType.top]
        },
        component: () => import("@/pages/home/my-zone.vue")
      }
    ]
  },
  //
  {
    path: "/personal",
    component: IndexPage,
    redirect: "/personal-data",
    children: [
      {
        path: "/personal-data",
        meta: { title: "", from: "/my-zone" },
        component: () => import("@/pages/user/personal-data.vue")
      },
      {
        path: "/help-use",
        meta: { title: "", from: "/my-zone" },
        component: () => import("@/pages/user/help-use.vue")
      },
      {
        path: "/my-stats",
        meta: { title: "", from: "/my-zone" },
        component: () => import("@/pages/user/my-stats.vue")
      },
      {
        path: "/about-us",
        meta: { title: "", cache: true, from: "/my-zone" },
        component: () => import("@/pages/user/about-us.vue")
      }
    ]
  },
  { path: "*", redirect: "/" } //
];

export default new Router({
  routes: routes
    .concat(courseRouter)
    .concat(courseHistoryRouter)
    .concat(courseSpaceRouter)
});



